
















































































































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import api from "../api";
import ImageUpload from "@/components/qTouch/imageUpload/ImageUpload.vue";
import AccessTeam from "@/microApps/openPlatform/components/AccessTeam.vue";
@Component({
  components: { AccessTeam, ImageUpload }
})
export default class AppDetail extends Vue {
  @Prop({ type: String })
  id!: string;
  @Prop({ type: String })
  method!: string;
  @Prop({ type: Number })
  authStatus!: number;

  appDetail: any = {
    type: [],
    types: []
  };
  fileList: Array<any> = [];
  loading: boolean = true;
  isPreview: boolean = this.method === "preview";
  showSecret: boolean = false;

  rules: any = {
    logoUrl: { required: true, trigger: "change", message: "请选择应用logo" },
    appName: { required: true, trigger: "blur", message: "请输入应用名称" },
    secureDomainName: { required: true, trigger: "blur", message: "请输入安全域名" },
    note: { required: true, trigger: "blur", message: "请输入应用简介" },
    types: { required: true, trigger: "change", message: "请至少选择一项终端类型" }
  };

  @Watch("id", { immediate: true })
  handleChangeId(newVal: any) {
    if (this.method === "create") {
      this.appDetail = {
        type: [],
        types: []
      };
      return;
    }
    if (newVal) {
      this.getAppDetail();
    }
  }

  @Watch("fileList")
  handleAttachChange(fileList: Array<any>) {
    if (fileList.length) {
      this.appDetail.logoUrl = fileList[0].url;
    } else {
      this.appDetail.logoUrl = "";
    }
  }

  get headerMessage() {
    if (this.method === "create") {
      return "创建轻应用";
    } else {
      if (this.isPreview) {
        return "查看轻应用";
      } else {
        return "修改轻应用";
      }
    }
  }
  get showHtmlEntry() {
    return this.appDetail.types.includes("H5");
  }
  get showWebEntry() {
    return this.appDetail.types.includes("WEB");
  }
  get showIosEntry() {
    return this.appDetail.types.includes("IOS");
  }
  get showAndroidEntry() {
    return this.appDetail.types.includes("ANDROID");
  }
  // 获取应用详情
  async getAppDetail() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${api.appDetail}?id=${this.id}`);
      if (status) {
        this.appDetail = data;
        this.$set(this.appDetail, "types", []);
        this.fileList = [{ url: this.appDetail.logoUrl, smallUrl: this.appDetail.logoUrl }];
        if (this.appDetail.type && this.appDetail.type.length) {
          this.changeDataTypes();
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }
  // 修改数据格式以正常显示和编辑
  changeDataTypes() {
    if (this.appDetail.type.filter((i: any) => i.terminalType === "H5").length) {
      this.appDetail.types.push("H5");
      this.appDetail.htmlEntry = this.appDetail.type.filter((i: any) => i.terminalType === "H5")[0].terminalEntrance;
    }
    if (this.appDetail.type.filter((i: any) => i.terminalType === "WEB").length) {
      this.appDetail.types.push("WEB");
      this.appDetail.webEntry = this.appDetail.type.filter((i: any) => i.terminalType === "WEB")[0].terminalEntrance;
      this.appDetail.webOpenMethod = this.appDetail.type.filter((i: any) => i.terminalType === "WEB")[0].openMode;
    }
    if (this.appDetail.type.filter((i: any) => i.terminalType === "IOS").length) {
      this.appDetail.types.push("IOS");
      this.appDetail.iosEntry = this.appDetail.type.filter((i: any) => i.terminalType === "IOS")[0].terminalEntrance;
    }
    if (this.appDetail.type.filter((i: any) => i.terminalType === "ANDROID").length) {
      this.appDetail.types.push("ANDROID");
      this.appDetail.androidEntry = this.appDetail.type.filter((i: any) => i.terminalType === "ANDROID")[0].terminalEntrance;
    }
  }
  // 及时更新视图
  changeForceUpdate(name: string, data: any) {
    this.$set(this.appDetail, name, data);
    this.$forceUpdate();
  }
  // 修改数据格式为服务器需求格式
  async submitApp() {
    this.appDetail.type = [];
    (this.$refs.appDetailForm as any).validate((valid: any) => {
      if (valid) {
        if (this.appDetail.types.includes("H5")) {
          this.appDetail.type.push({
            terminalType: "H5",
            terminalEntrance: this.appDetail.htmlEntry
          });
        }
        if (this.appDetail.types.includes("WEB")) {
          this.appDetail.type.push({
            terminalType: "WEB",
            terminalEntrance: this.appDetail.webEntry,
            openMode: this.appDetail.webOpenMethod
          });
        }
        if (this.appDetail.types.includes("IOS")) {
          this.appDetail.type.push({
            terminalType: "IOS",
            terminalEntrance: this.appDetail.iosEntry
          });
        }
        if (this.appDetail.types.includes("ANDROID")) {
          this.appDetail.type.push({
            terminalType: "ANDROID",
            terminalEntrance: this.appDetail.androidEntry
          });
        }
        this.doSave();
      }
    });
  }
  // 执行保存方法
  async doSave() {
    let {
      data: { data, status, message }
    } = await this.$http.post(this.method === "create" ? api.appCreate : api.appUpdate, {
      id: this.method === "create" ? "" : this.id,
      appName: this.appDetail.appName,
      logoUrl: this.appDetail.logoUrl,
      note: this.appDetail.note,
      secureDomainName: this.appDetail.secureDomainName,
      type: this.appDetail.type
    });
    if (status) {
      this.$message.success("提交成功，等待审核");
      this.$emit("success");
      this.$emit("cancel");
    } else {
      this.$message.error(message);
    }
  }
  // 取消审核
  async cancelApply() {
    this.$confirm("确认取消该轻应用申请?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.appCancel + "?id=" + this.id);
      if (status) {
        this.$message.success("取消成功");
        this.$emit("cancel");
      } else {
        this.$message.error(message);
      }
    });
  }
  async refreshSecret() {
    this.$confirm("是否刷新该应用密钥?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.appSecretRefresh + "?id=" + this.id);
      if (status) {
        this.$message.success("刷新成功");
        this.appDetail.appSecret = data;
        // this.$emit("cancel");
      } else {
        this.$message.error(message);
      }
    });
  }
}
