import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import Upload from "@/components/elementUI/upload";

@Component({
  components: {
    Upload
  }
})
export default class ImageUpload extends Vue {
  @Prop({ type: String })
  src!: string;
  @Prop({ type: Function, required: true })
  httpRequest!: () => void;
  @Prop({ type: Function, required: true })
  onChange!: () => void;
  created(): void {}
}
