



























































import { Component, Vue, Watch } from "vue-property-decorator";
import api from "../../api";
import AppDetail from "@/microApps/openPlatform/components/AppDetail.vue";

@Component({
  components: { AppDetail }
})
export default class ApplicationManagement extends Vue {
  searchTDO: any = {
    current: 1,
    query: {
      appName: "",
      status: ""
    },
    size: 15
  };
  appList: any[] = [];
  loading: boolean = true;
  isListPage: boolean = true;
  // btnDisabled: boolean = Number(localStorage.getItem("openPlatformAuth")) !== 2;
  total: number = 0;
  selectedApp: any = {};

  // created() {
  //   this.getAppList();
  // }

  @Watch("isListPage", { immediate: true })
  handleGetting(val: boolean) {
    if (val) this.getAppList();
  }

  get btnDisabled() {
    return Number(localStorage.getItem("openPlatformAuth")) !== 2;
  }

  async getAppList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.getAppList, this.searchTDO);
      if (status) {
        this.appList = data.records;
        this.total = data.total;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleCurrentChange(num: number) {
    this.searchTDO.current = num;
    this.getAppList();
  }

  createNewApp() {
    if (this.btnDisabled) {
      this.$message.error("您的资质审核未通过，暂无权限创建轻应用");
      return;
    }
    this.selectedApp = { method: "create" };
    this.isListPage = false;
  }

  getAppDetail(app: any) {
    if (this.btnDisabled) {
      this.$message.error("您的资质审核未通过，暂无权限访问应用详情");
      return;
    }
    this.selectedApp = app;
    this.selectedApp.method = "preview";
    this.isListPage = false;
  }

  beforeCreate() {
    this.$http.get(api.getQualificationData).then((res: any) => {
      if (res.data.status) {
        localStorage.setItem("openPlatformAuth", res.data.data.authStatus);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
}
