












































































import { Component, Prop, Vue } from "vue-property-decorator";
import api from "../api";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class AccessTeam extends Vue {
  @Prop({ type: String, required: true })
  appId!: string;

  searchForm: any = {
    current: 1,
    query: {
      teamName: "",
      status: ""
    },
    size: 10
  };
  total: number = 0;
  loading: boolean = true;
  applyTeamList: any[] = [];
  showApplyDialog: boolean = false;
  newApplyForm: any = {
    code: "",
    dataType: []
  };
  baseType: any[] = [];

  async getApplyTeamList(newSearch?: boolean) {
    if (newSearch) {
      this.searchForm.current = 1;
    }
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.getAccessTeamList + "?id=" + this.appId);
      if (status) {
        this.applyTeamList = data;
      }
    } finally {
      this.loading = false;
    }
  }

  async getBaseDataType() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getDataType, {
      params: {
        tenantId: this.newApplyForm.tenantId
      }
    });
    if (status) {
      this.baseType = data;
    }
  }

  handleCurrentChange(pageNum: number) {
    this.searchForm.current = pageNum;
    this.getApplyTeamList();
  }

  async checkCode() {
    if (this.newApplyForm.code) {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.checkAuthorizationCode + "?code=" + this.newApplyForm.code);
      if (status) {
        this.$set(this.newApplyForm, "tenantName", data.name);
        this.$set(this.newApplyForm, "tenantId", data.id);
        this.getBaseDataType();
      } else {
        this.$message.error(message);
      }
    } else {
      this.$message.warning("请输入授权码");
    }
  }

  async createNewApply() {
    (this.$refs.newApplyForm as ElForm).validate(async (valid: any) => {
      if (valid) {
        let {
          data: { data, status, message }
        } = await this.$http.post(api.createAccessTeam, {
          ...this.newApplyForm,
          id: this.appId
        });
        if (status) {
          this.$message.success("申请成功");
          this.showApplyDialog = false;
          this.getApplyTeamList(true);
        } else {
          this.$message.error(message);
        }
      }
    });
  }

  async cancelApply(item: any) {
    this.$confirm("确认取消接入?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.cancelAccessTeam + "?id=" + item.id);
      if (status) {
        this.$message.success("操作成功");
        this.getApplyTeamList();
      } else {
        this.$message.error(message);
      }
    });
  }

  created() {
    this.getApplyTeamList();
    // this.getBaseDataType();
  }
}
